@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #344b6c;
}

* {
  font-family: "Roboto";
  font-weight: 300;
}



.selectWrap {
    width: 80%;
    display: flex;
    flex-direction: row;
    height: 100px;
    justify-content: center;
    align-items: center;
}

select {
    background-color: #c4d2d6;
    color: #111152;
    display: inline-block;
    padding: 5px 10px;
    text-align-last: left;
}

.calculatorWrap {
    display: flex;
    flex-direction: row;
    width: 80%;
    justify-content: space-evenly;
}
.calculator {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 2px;
    background-color: #c4d2d6;
    padding: 40px 10px 10px 10px;
    width: 260px;
    height: 500px;
    font-family: "Montserrat";
}

.item {
    background-color: #111152;
    margin: 6px;
    padding: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1em;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.number {
    color: #fff;
    font-weight: bold;
}

.symbol {
    color: #a74b5c
}

.form {
    max-width: 100%;
    min-height: 100px;
    grid-column: 1/-1;
    display: flex;
    padding: 10px;
    justify-content: flex-end;
    align-items: center;
}
.form p {
    font-size: 25px;
    font-weight: bold;
    color: #506094;
    max-width: 100%;
    word-wrap: break-word;
}

.clean {
    grid-column: 1/3;
}

.back {
    grid-column: 3/-1;
}

.equal {
    grid-column: 4;
    grid-row: 6/8;
    background-color: #a74b5c;
    color: #fff
}

.zero {
    grid-column: 1/3;
}

.result {
    width: 500px;
    height: 170px;
    border: 4px solid #c4d2d6;
    background-color: #111152;
    border-radius: 5px;
}

.result p {
    text-align-last: left;
    padding: 10px;
    color: #fff;
    margin: 0;
}

.instruction {
    width: 508px;
    background-color: #c4d2d6;
    margin-top: 50px;
    text-align: left;
}

.instruction h2 {
    font-weight: normal;
    color: #fff;
    padding: 10px;
    font-size: 1.2em;
}

.instruction p {
    font-size: 0.8em;
    padding: 20px 10px;
    color: #111152;
}
